@font-face {
  font-family: PWix;
  src: url("bahij_janna-regular-webfont.3c90a53e.woff2") format("woff2"), url("bahij_janna-regular-webfont.d5f8a8ec.woff") format("woff");
}

@font-face {
  font-family: PWixBold;
  src: url("bahij_janna-bold-webfont.0d6eeba1.woff2") format("woff2"), url("bahij_janna-bold-webfont.cd1ce669.woff") format("woff");
}

:root {
  --mainColor: #29ab87;
  --secondaryColor: #298eab;
  --white: #efefef;
}

body {
  z-index: 0;
  background-color: var(--white);
  text-justify: auto;
  font-family: PWix;
  font-size: large;
}

p {
  text-align: left;
  padding: 0 2em;
  font-family: PWix;
  line-height: 1.25em;
}

strong, h1 {
  color: var(--mainColor);
}

h1 {
  font-family: PWixBold;
  font-size: 3em;
}

h2 {
  text-align: center;
  width: 100%;
  font-family: PWixBold;
  font-size: 1.5em;
}

h3 {
  margin: 0;
  padding: 0;
  font-family: PWixBold;
}

h2, h3, a {
  color: var(--secondaryColor);
}

ul {
  flex-direction: column;
  justify-content: space-between;
  height: 50%;
  margin: 1.5em;
  padding: 0;
  list-style: none;
  display: flex;
}

.underlined {
  text-decoration: underline;
}

.hidden {
  display: none;
}

.visible {
  z-index: 1000;
  width: 50%;
  display: flex;
}

ul a {
  color: var(--white);
  font-family: PWixBold;
  font-size: larger;
  text-decoration: none;
}

details:hover {
  cursor: pointer;
}

@keyframes slide {
  from {
    transform: translate(-1000px);
  }

  to {
    transform: translate(0);
  }
}

nav {
  float: left;
  background-color: var(--mainColor);
  place-items: center;
  height: 100vh;
  font-family: Courier New, Courier, monospace;
  font-size: large;
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
}

body nav + nav {
  z-index: 1;
  font-size: medium;
}

body > div img {
  width: 50px;
  height: 50px;
  position: fixed;
  top: 0;
  left: 0;
}

body > div {
  background-color: var(--mainColor);
  z-index: 10000;
  border-radius: 0 0 10px;
  width: 50px;
  height: 50px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

html, body {
  flex-direction: row;
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 0;
}

main {
  float: left;
  background-color: var(--white);
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

figure {
  justify-content: center;
  display: flex;
}

body nav + nav {
  background-color: var(--secondaryColor);
  flex-direction: column;
  flex-basis: 200px;
  justify-content: center;
  align-items: start;
  display: flex;
}

body nav + nav details {
  margin: 1em;
}

body nav + nav li {
  margin-top: 1em;
  list-style: disc;
}

main[activePage="Intro"] {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

main[activePage="Intro"] article {
  flex-direction: row;
  align-items: center;
  display: flex;
}

main[activePage="Intro"] article > figure img {
  max-width: 100%;
  height: auto;
}

main[activePage="Intro"] article:last-child {
  justify-content: space-evenly;
  margin-top: 5%;
}

main[activePage="Intro"] article:last-child img {
  height: 10vh;
  margin: 1em;
}

main[activePage="Intro"] article:last-child img:hover {
  cursor: pointer;
}

main[activePage="Projects"] {
  flex-direction: column;
  align-items: center;
  display: flex;
}

main[activePage="Projects"] img {
  width: 40vh;
}

main[activePage="Projects"] div {
  flex-direction: column;
  align-items: center;
  display: flex;
}

main[activePage="Projects"] .project-container {
  flex-direction: row-reverse;
  display: flex;
}

main[activePage="Projects"] .previousButton {
  transform: rotate(180deg);
}

main[activePage="Projects"] .image-button {
  border: none;
  width: 50px;
  height: 50px;
}

main[activePage="Projects"] .figures-container {
  flex-direction: row-reverse;
  display: flex;
}

main[activePage="Projects"] .figures-container figure {
  margin: 0;
}

main[activePage="Projects"] .image-button:hover {
  cursor: pointer;
}

main[activePage="Education"] {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

main[activePage="Education"] section {
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 70%;
  margin: 1em;
  padding: 0 1em;
  display: flex;
  box-shadow: 1px 1px 3px 1px;
}

main[activePage="Education"] section > div {
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-width: 100px;
  display: flex;
}

main[activePage="Education"] section > div h3, main[activePage="Education"] section > div h2 {
  margin: 0;
  padding: 0;
}

main[activePage="Education"] img {
  max-width: 100%;
  height: auto;
  margin: auto;
  display: block;
}

@media (height < 600px), (width < 1000px) {
  main[activePage="Intro"] article:last-child {
    display: none;
  }

  main[activePage="Intro"], main[activePage="Intro"] article {
    flex-direction: column;
  }

  main[activePage="Intro"] article img {
    width: 70%;
  }

  body main {
    margin-top: 1em !important;
  }

  p {
    font-size: .9em;
  }

  h1 {
    font-size: 1.5em;
  }

  h2, h3 {
    font-size: 1.2em;
  }

  body > div {
    display: block;
  }

  nav {
    width: 50%;
    animation: .25s slide;
    position: fixed;
    left: 0;
  }

  body nav + nav {
    left: 50%;
  }

  main[activePage="Projects"] div.project-container {
    flex-direction: column-reverse;
  }

  main[activePage="Projects"] {
    flex-direction: column;
  }

  main[activePage="Projects"] img {
    width: 30vh;
  }
}

